import { Switch, Route, Redirect } from 'react-router-dom';
import { NavHashLink, HashLink } from 'react-router-hash-link';
import Projects from './Projects'

function App() {
  return (
    <>
      <div style={{ position: 'fixed', right: 0 }}>
        <NavHashLink smooth to="/#home">Home</NavHashLink>
        <NavHashLink smooth to="/#about">About</NavHashLink>
        <NavHashLink smooth to="/#projects">Projects</NavHashLink>
        <NavHashLink smooth to="/#contact">Contact</NavHashLink>
      </div>

      <Switch>
        <Route exact path='/'>
          <div id='home' style={{ marginBottom: '100vh' }}>Home</div>
          <div id='about' style={{ marginBottom: '100vh' }}>About</div>
          <div id='projects' style={{ marginBottom: '100vh' }}>
            Projects
            <ul>
              <li>
                <HashLink to='/projects/testing1'>
                  Testing1
                </HashLink>
              </li>
              <li>
                <HashLink to='/projects/testing2'>
                  Testing2
                </HashLink>
              </li>
            </ul>
          </div>
          <div id='contact' style={{ marginBottom: '100vh' }}>Contact</div>
        </Route>

        <Route exact path='/projects'>
          <Redirect to='/#projects' />
        </Route>
        <Route exact path='/projects/:project'>
          <Projects />
        </Route>
      </Switch>
    </>
  );
}

export default App;
